import Heading from '../../components/Heading/Heading'
import { Reveal } from '../utils/Reveal/Reveal'

import './parcours.css'

function Parcours() {
  return (
    <div className="parcours-container">
      <Heading beforeText="" italicText="PAR" afterText="COURS" margin={false} />
      <div className="parcours-list">
        <Reveal>
          <div className="parcours-item">
            <p>2022 - 2023 </p>
            <p>
              <span>Bac+3/4 :</span> Bachelor  Dveloppeur d'Applications - Javascript React  chez
              OpenClassRooms
            </p>
          </div>
        </Reveal>
        <Reveal>
          <div className="parcours-item">
            <p>2019 - 2021 </p>
            <p>
              <span>Bac+2 :</span> Brevet de Technicien Suprieur de Services Informatiques aux
              Organisations (SIO) au Lyce Victor et Hlne Basch (VHB)  Rennes
            </p>
            <p>Option: Solutions Logicielles et Applications Mtiers (SLAM)</p>
          </div>
        </Reveal>
      </div>
    </div>
  )
}

export default Parcours
